import React from 'react';
import Card from '../Card/Card';
import './services.css';
import Heart from '../../img/heartemoji.png';
import Glasses from '../../img/glasses.png';
import Humble from '../../img/humble.png';
import Profile from './Profile.pdf'
import { themeContext } from "../../Context";
import { useContext } from "react";
import {motion} from '../../../node_modules/framer-motion/dist/framer-motion'

const Services = () => {
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;

  const transition = {duration : 1, type: 'spring'}
  return (
    <div className="services" id='Services'>
        <div className="awesome">
            <span style={{color: darkMode? 'white': ''}}>My Awesome</span>
            <span>Services</span>
            <span>Lorem ipsum dolor sit amet consectetur adipisicing elit. <br/>
            Quibusdam maxime adipisci possimus</span>
            <a href={Profile} download>

            <button className="button s-button">Download CV</button>
            </a>

            <div className="blur s-blur1" style={{background: "#ABF1FF94"}}></div>

        </div>
        <div className="cards">
        <motion.div 
        initial={{left:"25rem"}}
        whileInView={{left: "16rem"}}
        transition={transition}
        
        
        style={{left: '20rem'}}>

            <Card emoji={Heart}
                heading={'Design'}
                detail={'Figma, Sketch, Photoshop, Adobe, Adobe xd'}/>
         </motion.div>

         <motion.div 
         initial={{left:"-10rem"}}
         whileInView={{left: "1rem"}}
         transition={transition}
         
         
         
         style={{top: '12rem', left: '2rem'}}>
            <Card emoji={Glasses}
            heading={"Developer"}
            detail={"Html, Css, JavaScript, React"}/>
         </motion.div>

         <motion.div 
         initial={{left:"25rem"}}
         whileInView={{left: "18rem"}}
         transition={transition}
         
         
         
         style={{top: '19rem', left: '18rem'}}>
            <Card emoji={Humble}
                  heading={"UI/UX"}
                  detail={'Lorem ipsum dolor sit amet consectetur adipisicing elit'}/>
                  
         </motion.div>
         <div className="blur s-blur2" style={{background: 'var(--purple)', }}></div>

        </div>
    </div>
  )
}

export default Services