import React from 'react';
import FloatingDiv from '../Floatingdiv/FloatingDiv';

import Github from "../../img/github.png";
import Linkedin from "../../img/linkedin.png";
import Instagram from "../../img/instagram.png";
import Vector1 from "../../img/Vector1.png";
import Men from "../../img/men.png";
import Vector2 from "../../img/Vector2.png";
import Thumbup from "../../img/thumbup.png";
import Crown from "../../img/crown.png";
import Glassemoji from "../../img/glassesimoji.png";
import { themeContext } from "../../Context";
import { useContext } from "react"
import {motion} from '../../../node_modules/framer-motion/dist/framer-motion'
import './intro.css';


const Intro = () => {

    const transition = {duration : 2, type: 'spring'}

    const theme = useContext(themeContext);
    const darkMode = theme.state.darkMode;
    return (
        <div className="intro">
            <div className="i-left">
                <div className="i-name">
                    <span style={{ color: darkMode ? 'white' : '' }}>Hi! I Am</span>
                    <span>Shehruz Nehmetzade</span>
                    <span>Front-End Developer with high
                        level of experience in web designing
                        and development, producting the
                        Quality work.</span>
                </div>
                <button className="button i-button">Hire me</button>
                <div className="i-icons">
                    <a href="https://github.com/sehruzCode" target={'_blank'}>
                        <img src={Github} alt="" />
                    </a>
                    <a href="https://www.linkedin.com/in/%C5%9F%C9%99hruz-n%C9%99hm%C9%99tov-21b17125b" target={'_blank'}>
                        <img src={Linkedin} alt="" />
                    </a>
                    <a href="https://www.instagram.com/shehruznehmetov/" target={'_blank'}>

                        <img src={Instagram} alt="" />
                    </a>
                </div>
            </div>
            <div className="i-rigth">
                <img src={Vector1} alt="" />
                <img src={Vector2} alt="" />
                <img src={Men} alt="" width={600} />
                <motion.img
                initial={{left: '-40%'}}
                whileInView= {{left: '-24%'}}
                transition={transition}
                src={Glassemoji} alt="" />
                <motion.div 
                initial={{top: '-4%', left: '74%'}}
                whileInView={{left: '68%'}}
                transition={transition}
                style={{ top: '4%', left: '68%' }} 
                className='floating-div'>
                    <FloatingDiv image={Crown} text1='Web' text2='Developer' />
                </motion.div>
                <motion.div 
                initial={{left: '9rem', top: '18rem'}}
                whileInView={{left: "0rem"}}
                transition={transition}
                
                style={{ top: '18rem', left: '0rem' }}
                className='floating-div'>
                    <FloatingDiv image={Thumbup} text1='Best Design' text2='Award' />
                </motion.div>
                <div className='blur' style={{ background: "rgba(238 210 255)" }}></div>
                <div className="blur" style={{
                    background: '#C1F5FF',
                    top: '17rem',
                    width: '21rem',
                    height: '11rem',
                    left: '-9rem'
                }}></div>
            </div>
        </div>
    )
}

export default Intro
