import React from 'react';
import './portfolio.css'; 
import {Swiper,SwiperSlide} from 'swiper/react'
import Sidebar from '../../img/newage.png'
import Ecommerce from '../../img/newage2.png'
import HOC from '../../img/casual2.png'
import MusicApp from '../../img/casual.png'
import Creative from '../../img/creative.png'
import Clean from '../../img/clean blog.png'
import 'swiper/css'
import { themeContext } from "../../Context";
import { useContext } from "react";

const Portfolio = () => {
    const theme = useContext(themeContext);
    const darkMode = theme.state.darkMode;
  return (
    <div className="portfolio" id='Portfolio'>
        <span style={{color: darkMode? 'white': ''}}>Reject Projects</span>
        <span>Portfolio</span>

        <Swiper
        spaceBetween={30}
        slidesPerView={3}
        grabCursor={true}
        className='portfolio-slider'
        >
            <SwiperSlide className='swiper-slide'>
                <img src={Sidebar} alt="" />
            </SwiperSlide>
            <SwiperSlide className='swiper-slide'>
                <img src={Ecommerce} alt="" />
            </SwiperSlide>
            <SwiperSlide className='swiper-slide'>
                <img src={MusicApp} alt="" />
            </SwiperSlide>
            <SwiperSlide className='swiper-slide'>
                <img src={HOC} alt="" />
            </SwiperSlide>
            <SwiperSlide className='swiper-slide'>
                <img src={Clean} alt="" />
            </SwiperSlide>
            <SwiperSlide className='swiper-slide'>
                <img src={Creative} alt="" />
            </SwiperSlide>
        </Swiper>
    </div>
  )
}

export default Portfolio